import React , {useEffect , useState} from "react";
import axios from 'axios'
import {HashRouter as Router , Switch  , Route} from 'react-router-dom';
import Homepage from './components/Homepage';
import Questions from './components/Questions';
import './App.css';  
function App() {

  const [data, setData]=useState([]);
  const [index,setIndex]=useState(0);
  const [difficulty,setdifficulty]=useState('');
  useEffect(() => {
    axios.get("https://opentdb.com/api_category.php").then( res=> setData(res.data.trivia_categories));
  }, [])
  return (
    <div className="App">
    <Router>
      <Switch>
        <Route path='/' exact> <Homepage data={data} setIndex={setIndex} setdifficulty={setdifficulty} index={index} difficulty={difficulty} /></Route>
        <Route path='/questions'> <Questions index={index} difficulty={difficulty} /> </Route>
      </Switch>
    </Router>
    </div>
  );
}
export default App;
