import React from 'react';
import {Link} from 'react-router-dom';
import './Css/homepage.css';


const Homepage = ({data, setIndex, setdifficulty,index,difficulty}) => {
    if(data === undefined){
        return 'Loading'
    }
    return (
        <div className="Homepage-Container" >
            <h1>Welcome to our <strong>Quiz Game.</strong></h1>
            <h2>Good luck !</h2>
            <br/>
            <hr style={{backgroundColor:"rgb(174, 179, 179)" , height:'3px', border:'1px solid white'}} />
            <div className="Category-Container" >
                <h3>Select Category</h3>
                <select onChange={e=>setIndex(e.target.value)} name="Category" defaultValue='Category'>
                    <option  disabled >Category</option>
                    {data.map(res=><option key={res.id} value={res.id} >{res.name}</option>)}
                </select>
            </div> 
            <div className='Difficulty-Container' >
            <h3>Select Difficulty</h3>
            <select onChange={e=>setdifficulty(e.target.value)} name="Difficulty" defaultValue="Difficulty">
                <option disabled>Difficulty</option>
                <option value="easy" >Easy</option>
                <option value="medium">Medium</option>
                <option value="hard">Hard</option>
            </select>
            </div>
            <div>
                {
                index===0 || difficulty==="" ?
                    <button className="start-btn" style={{opacity:".4"}} disabled >Start</button>
                    :
                    <button className="start-btn" type="button"> <Link  className='start-link' to="/questions" >Start</Link> </button>
                }
            </div>
        </div>
    )
}

export default Homepage
