import React,{useEffect,useState} from 'react'
import {Link} from 'react-router-dom';
import axios from 'axios';
import './Css/questions.css';
const Questions = ({index , difficulty}) => {

    const[questions,setQuestions]=useState([]);
    const[next,setNext]=useState(0);
    const[score,setScore]=useState(0);
    let  shuffledArray=[];

    useEffect(() => {
        axios.get(`https://opentdb.com/api.php?amount=10&category=${index}&difficulty=${difficulty}`).then(res=>setQuestions(res.data.results));
    }, [])

    if(questions.length === 0){
        return(
        <div className="bouncer" >
            <div className="ball" ></div>
            <div className="ball" ></div>
            <div className="ball" ></div>
            <div className="ball" ></div>
        </div>
        )
    }   
   
    if(next<10){
        shuffledArray = [questions[next].correct_answer , questions[next].incorrect_answers[0] , questions[next].incorrect_answers[1] , questions[next].incorrect_answers[2]].sort(() => Math.floor(Math.random() - 0.5));
    }
    
    const answers=(e,k)=>{
        if(e===questions[next].correct_answer){
            k.style.backgroundColor='green';
            k.style.color='#fff';
            k.style.transition="all 0.5s ease-in";
        }else{
            k.style.backgroundColor='rgb(247, 17, 17)'
            k.style.color='#fff';
            k.style.transition="all 0.5s ease-in";
        }
        setTimeout(() => {
            if(e===questions[next].correct_answer){
                setScore(score+1);
            }
           setNext(next+1);
        }, 1500);
    }

    const Quiz=()=>(
        <div className="Homepage-Container">
            <p id='p' dangerouslySetInnerHTML={{__html:questions[next].question}} />
            <br/>
            <hr/>
            <ul>
                {shuffledArray.map((e,index)=> <li dangerouslySetInnerHTML={{__html:e}}key={index} onClick={(k)=>{answers(e,k.target)}} ></li>)}
            </ul>
            <button className='start-btn' onClick={()=>setNext(next+1)} >Skip</button>
        </div>
    )

    const End=()=>(
        <div className="end-Container" >
            <p> Final Result</p>  
            <div className="circle" >
                <p>{score}/10</p>
            </div>
            {score >5 ?<p>Well Done!</p>:<p>you can more Try again!</p> }
            
            <button className="start-btn" type="button"> <Link  className='start-link' to="/" >New Test</Link> </button>
            <button className="start-btn" type="button" onClick={()=>{setNext(0); setScore(0)}}>Try again  </button>
        </div>
    )
    return (
    <div>
        {next<10 ? <Quiz /> : <End />}
    </div>
    )
}
export default Questions
